    },
    parseJSONorNot: function(mayBeJSON) {
      if (typeof mayBeJSON === "string") {
        return JSON.parse(mayBeJSON);
      } else {
        return mayBeJSON;
      }
    },
    UDFCompatibleDatafeed: function(updateFrequency) {
      this._configuration = undefined;
      this._symbolSearch = null;
      this._symbolsStorage = null;
      this._barsPulseUpdater = this.DataPulseUpdater(
        this,
        updateFrequency || 10 * 1000
      );
      this._quotesPulseUpdater = this.QuotesPulseUpdater(this);
      this._enableLogging = false;
      this._initializationFinished = false;
      this._callbacks = {};
      this._initialize();
      return this;
    },
    _initialize: function() {
      this._setupWithConfiguration(this.defaultConfiguration());
    },
    defaultConfiguration: function() {
      return {
        supports_search: true,
        supports_group_request: false,
        //分辨率
        supported_resolutions: ["1", "5", "15", "30", "60", "D", "W"],
        //标识datafeed是否支持在L线上显示标记
        supports_marks: false,
        //是否支持时间刻度标记
        supports_timescale_marks: false,
      };
    },
    _setupWithConfiguration: function(configurationData) {
      this._configuration = configurationData;

      if (!configurationData.exchanges) {
        configurationData.exchanges = [];
      }
      //	@obsolete; remove in 1.5
      var supportedResolutions =
        configurationData.supported_resolutions ||
        configurationData.supportedResolutions;
      configurationData.supported_resolutions = supportedResolutions;

      //	@obsolete; remove in 1.5
      var symbolsTypes =
        configurationData.symbols_types || configurationData.symbolsTypes;
      configurationData.symbols_types = symbolsTypes;

      if (
        !configurationData.supports_search &&
        !configurationData.supports_group_request
      ) {
        throw new Error(
          "Unsupported datafeed configuration. Must either support search, or support group request"
        );
      }

      if (!configurationData.supports_search) {
        that._symbolSearch = that.SymbolSearchComponent(this);
      }

      if (configurationData.supports_group_request) {
        //	this component will call onInitialized() by itself
        this._symbolsStorage = that.SymbolsStorage(this);
      } else {
        this.onInitialized();
      }

      this._fireEvent("configuration_ready");
      this._logMessage("Initialized with " + JSON.stringify(configurationData));
    },
    onInitialized: function() {
      this._initializationFinished = true;
      this._fireEvent("initialized");
    },
    _fireEvent: function(event, argument) {
      if (this._callbacks.hasOwnProperty(event)) {
        var callbacksChain = this._callbacks[event];
        for (var i = 0; i < callbacksChain.length; ++i) {
          callbacksChain[i](argument);
        }
        this._callbacks[event] = [];
      }
    },
    _logMessage: function(message) {
      if (this._enableLogging) {
        var now = new Date();
      }
    },
    onReady: function(callback) {
      var that = this;
      if (this._configuration) {
        setTimeout(function() {
          callback(that._configuration);
        }, 0);
      } else {
        this.on("configuration_ready", function() {
          callback(that._configuration);
        });
      }
    },
    on: function(event, callback) {
      if (!this._callbacks.hasOwnProperty(event)) {
        this._callbacks[event] = [];
      }
      this._callbacks[event].push(callback);
      return this;
    },
    _send: function(method, params, callback) {
      params.method = method;
      var jsonStr = JSON.stringify(params);
      console.log(jsonStr)
      this.send(jsonStr, callback);
    },
    SymbolsStorage: function(datafeed) {
      this._datafeed = datafeed;

      this._exchangesList = ["NYSE", "FOREX", "AMEX"];
      this._exchangesWaitingForData = {};
      this._exchangesDataCache = {};

      this._symbolsInfo = {};
      this._symbolsList = [];

      this._requestFullSymbolsList();
