    resolveSymbol: function(
      symbolName,
      onSymbolResolvedCallback,
      onResolveErrorCallback
    ) {
      var that = this;

      setTimeout(function() {
        if (!that._symbolsInfo.hasOwnProperty(symbolName)) {
          onResolveErrorCallback("invalid symbol");
        } else {
          onSymbolResolvedCallback(that._symbolsInfo[symbolName]);
        }
      }, 0);
    },
    SymbolSearchComponent: function(datafeed) {
      this._datafeed = datafeed;
    },
    searchSymbols: function(searchArgument, maxSearchResults) {
      if (!this._datafeed._symbolsStorage) {
        throw new Error(
          "Cannot use local symbol search when no groups information is available"
        );
      }

      var symbolsStorage = this._datafeed._symbolsStorage;

      var results = []; // array of WeightedItem { item, weight }
      var queryIsEmpty =
        !searchArgument.searchString ||
        searchArgument.searchString.length === 0;
      var searchStringUpperCase = searchArgument.searchString.toUpperCase();

      for (var i = 0; i < symbolsStorage._symbolsList.length; ++i) {
        var symbolName = symbolsStorage._symbolsList[i];
        var item = symbolsStorage._symbolsInfo[symbolName];

        if (
          searchArgument.type &&
          searchArgument.type.length > 0 &&
          item.type !== searchArgument.type
        ) {
          continue;
        }

        if (
          searchArgument.exchange &&
          searchArgument.exchange.length > 0 &&
          item.exchange !== searchArgument.exchange
        ) {
          continue;
        }

        var positionInName = item.name
          .toUpperCase()
          .indexOf(searchStringUpperCase);
        var positionInDescription = item.description
          .toUpperCase()
          .indexOf(searchStringUpperCase);

        if (queryIsEmpty || positionInName >= 0 || positionInDescription >= 0) {
          var found = false;
          for (
            var resultIndex = 0;
            resultIndex < results.length;
            resultIndex++
          ) {
            if (results[resultIndex].item === item) {
              found = true;
              break;
            }
          }

          if (!found) {
            var weight =
              positionInName >= 0
                ? positionInName
                : 8000 + positionInDescription;
            results.push({ item: item, weight: weight });
          }
        }
      }

      searchArgument.onResultReadyCallback(
        results
          .sort(function(weightedItem1, weightedItem2) {
            return weightedItem1.weight - weightedItem2.weight;
          })
          .map(function(weightedItem) {
            var item = weightedItem.item;
            return {
              symbol: item.name,
              full_name: item.full_name,
              description: item.description,
              exchange: item.exchange,
              params: [],
              type: item.type,
              ticker: item.name
            };
          })
          .slice(0, Math.min(results.length, maxSearchResults))
      );
    },
    DataPulseUpdater: function(datafeed, updateFrequency) {
      this._datafeed = datafeed;
      this._subscribers = {};

      this._requestsPending = 0;
      var that = this;

      var update = function() {
        if (that._requestsPending > 0) {
          return;
        }

        for (var listenerGUID in that._subscribers) {
          var subscriptionRecord = that._subscribers[listenerGUID];
          var resolution = subscriptionRecord.resolution;

          var datesRangeRight = parseInt(new Date().valueOf() / 1000);

          //	BEWARE: please note we really need 2 bars, not the only last one
          //	see the explanation below. `10` is the `large enough` value to work around holidays
          var datesRangeLeft =
            datesRangeRight - that.periodLengthSeconds(resolution, 10);

          that._requestsPending++;

          (function(_subscriptionRecord) {
            // eslint-disable-line
            that.getBars(
              _subscriptionRecord.symbolInfo,
              resolution,
              datesRangeLeft,
              datesRangeRight,
              function(bars) {
                that._requestsPending--;

                //	means the subscription was cancelled while waiting for data
                if (!that._subscribers.hasOwnProperty(listenerGUID)) {
                  return;
                }

                if (bars.length === 0) {
                  return;
                }

                var lastBar = bars[bars.length - 1];
                if (
                  !isNaN(_subscriptionRecord.lastBarTime) &&
                  lastBar.time < _subscriptionRecord.lastBarTime
                ) {
                  return;
                }

                var subscribers = _subscriptionRecord.listeners;

                //	BEWARE: this one isn't working when first update comes and this update makes a new bar. In this case
                //	_subscriptionRecord.lastBarTime = NaN
                var isNewBar =
                  !isNaN(_subscriptionRecord.lastBarTime) &&
                  lastBar.time > _subscriptionRecord.lastBarTime;

                //	Pulse updating may miss some trades data (ie, if pulse period = 10 secods and new bar is started 5 seconds later after the last update, the
                //	old bar's last 5 seconds trades will be lost). Thus, at fist we should broadcast old bar updates when it's ready.
                if (isNewBar) {
                  if (bars.length < 2) {
                    throw new Error(
                      "Not enough bars in history for proper pulse update. Need at least 2."
                    );
                  }

                  var previousBar = bars[bars.length - 2];
                  for (var i = 0; i < subscribers.length; ++i) {
                    subscribers[i](previousBar);
                  }
                }

                _subscriptionRecord.lastBarTime = lastBar.time;

                for (var i = 0; i < subscribers.length; ++i) {
                  subscribers[i](lastBar);
                }
              },

              //	on error
              function() {
                that._requestsPending--;
              }
            );
          })(subscriptionRecord);
        }
      };

      if (typeof updateFrequency != "undefined" && updateFrequency > 0) {
        this.k_timer = setInterval(update, updateFrequency);
      }
    },
    unsubscribeDataListener: function(listenerGUID) {
      this._datafeed._logMessage("Unsubscribing " + listenerGUID);
      delete this._subscribers[listenerGUID];
    },
    subscribeDataListener: function(
      symbolInfo,
      resolution,
      newDataCallback,
      listenerGUID
    ) {
      this._datafeed._logMessage("Subscribing " + listenerGUID);

      if (!this._subscribers.hasOwnProperty(listenerGUID)) {
        this._subscribers[listenerGUID] = {
          symbolInfo: symbolInfo,
          resolution: resolution,
          lastBarTime: NaN,
          listeners: []
        };
      }

      this._subscribers[listenerGUID].listeners.push(newDataCallback);
    },
    periodLengthSeconds: function(resolution, requiredPeriodsCount) {
      var daysCount = 0;

      if (resolution === "D") {
        daysCount = requiredPeriodsCount;
      } else if (resolution === "M") {
        daysCount = 31 * requiredPeriodsCount;
      } else if (resolution === "W") {
        daysCount = 7 * requiredPeriodsCount;
      } else {
        daysCount = (requiredPeriodsCount * resolution) / (24 * 60);
      }

      return daysCount * 24 * 60 * 60;
    },
    QuotesPulseUpdater: function(datafeed) {
      this._datafeed = datafeed;
      this._subscribers = {};
      this._updateInterval = 60 * 1000;
      this._fastUpdateInterval = 10 * 1000;
      this._requestsPending = 0;

      var that = this;

      /*setInterval(function() {
                    that._updateQuotes(function(subscriptionRecord) { return subscriptionRecord.symbols; });
                }, this._updateInterval);

                setInterval(function() {
                    that._updateQuotes(function(subscriptionRecord) { return subscriptionRecord.fastSymbols.length > 0 ? subscriptionRecord.fastSymbols : subscriptionRecord.symbols; });
                }, this._fastUpdateInterval);*/
    },
    /*subscribeDataListener :function(symbols, fastSymbols, newDataCallback, listenerGUID) {
                console.log("00000000000")
                if (!this._subscribers.hasOwnProperty(listenerGUID)) {
                    this._subscribers[listenerGUID] = {
                        symbols: symbols,
                        fastSymbols: fastSymbols,
                        listeners: []
                    };
                }

                this._subscribers[listenerGUID].listeners.push(newDataCallback);
            },

             unsubscribeDataListener : function(listenerGUID) {
                delete this._subscribers[listenerGUID];
            },*/
    _updateQuotes: function(symbolsGetter) {
      if (this._requestsPending > 0) {
        return;
      }

      var that = this;
      for (var listenerGUID in this._subscribers) {
        this._requestsPending++;

        var subscriptionRecord = this._subscribers[listenerGUID];
        this._datafeed.getQuotes(
          symbolsGetter(subscriptionRecord),

          // onDataCallback
          (function(subscribers, guid) {
            // eslint-disable-line
            return function(data) {
              that._requestsPending--;

              // means the subscription was cancelled while waiting for data
              if (!that._subscribers.hasOwnProperty(guid)) {
                return;
              }

              for (var i = 0; i < subscribers.length; ++i) {
                subscribers[i](data);
              }
            };
          })(subscriptionRecord.listeners, listenerGUID),
          // onErrorCallback
          function(error) {
            that._requestsPending--;
          }
        );
      }
    },
    /*if (typeof module !== 'undefined' && module && module.exports) {
            module.exports = {
                UDFCompatibleDatafeed: Datafeeds.UDFCompatibleDatafeed,
            };
        }*/
    subscribeBars: function(
      symbolInfo,
      resolution,
      onRealtimeCallback,
      listenerGUID,
      onResetCacheNeededCallback
    ) {
      this.subscribeDataListener(
        symbolInfo,
        resolution,
        onRealtimeCallback,
        listenerGUID,
        onResetCacheNeededCallback
      );
    },
    unsubscribeBars: function(listenerGUID) {
      this.unsubscribeDataListener(listenerGUID);
    },
    resolveSymbol: function(
      symbolName,
      onSymbolResolvedCallback,
      onResolveErrorCallback
    ) {
      var that = this;
      if (!that._initializationFinished) {
        this.on("initialized", function() {
          that.resolveSymbol(
            symbolName,
            onSymbolResolvedCallback,
            onResolveErrorCallback
          );
        });
        return;
      }
      var resolveRequestStartTime = Date.now();
      that._logMessage("Resolve requested");

      function onResultReady(data) {
        var postProcessedData = data;
        if (that.postProcessSymbolInfo) {
          postProcessedData = that.postProcessSymbolInfo(postProcessedData);
        }
        that._logMessage(
          "Symbol resolved: " + (Date.now() - resolveRequestStartTime)
        );
        onSymbolResolvedCallback(postProcessedData);
      }

      if (!that._configuration.supports_group_request) {
        this._send(
          "symbols",
          {
            tradeId: that.tradeId
          },
          function(response) {
            var data = that.parseJSONorNot(response);
            if (data.code && data.code != 0) {
              onResolveErrorCallback("unknown_symbol");
            } else {
              onResultReady(data.obj);
            }
          }
        );
      } else {
        if (that._initializationFinished) {
          that._symbolsStorage.resolveSymbol(
            symbolName,
            onResultReady,
            onResolveErrorCallback
          );
        } else {
          this.on("initialized", function() {
            that._symbolsStorage.resolveSymbol(
              symbolName,
              onResultReady,
              onResolveErrorCallback
            );
          });
        }
      }
    },
    getBars: function(
      symbolInfo,
      resolution,
      rangeStartDate,
      rangeEndDate,
      onDataCallback,
      onErrorCallback
    ) {
      var that = this;
      //	timestamp sample: 1399939200
      if (rangeStartDate > 0 && (rangeStartDate + "").length > 10) {
        throw new Error([
          "Got a JS time instead of Unix one.",
          rangeStartDate,
          rangeEndDate
        ]);
      }

      var param = {
        tradeId: symbolInfo.ticker.toUpperCase(),
        resolution: resolution,
        from: rangeStartDate,
        to: rangeEndDate
      };

      this._send("kData", param, function(response) {
        var data = that.parseJSONorNot(response);

        var nodata = data.code == 0 && data.obj.length <= 0;

        if (data.code !== 0 && !nodata) {
          if (!!onErrorCallback) {
            onErrorCallback(data.code);
          }
          return;
        }
        var bars = data.obj;
   console.log(data.obj)
        onDataCallback(bars, {
          noData: nodata,
          nextTime: data.nb || data.nextTime
        });

      });
    }
  }
};
</script>
<style lang="less" scoped>
.full {
  background: #131e31;
