      position: absolute;
      top: 2.1rem;
      width: 60%;
      font-size: 0.24rem;
      height: 0.5rem;
      z-index: 10000;
      display: flex;
      color: #7678a5;
      padding: 0 0.2rem;
      span {
        flex: 1;
        display: inline-block;
        text-align: center;
        margin-right: 0.3rem;
        display: inline-block;
        line-height: 0.5rem;
        font-size: 0.2rem;
      }
      .active2 {
        color: #fff;
        border-bottom: 0.02rem solid #fff;
      }
    }
    .headerInfo {
      width: 100%;
      padding: 0 0.3rem 0.2rem 0.3rem;
      display: flex;
      justify-content: space-between;
      height: 1.52rem;
      background: #131e31;
      align-items: center;
      color: #fff;
      line-height: 1;
      // &.increase {
      //   .total {
      //     color: #F43364 ;
      //   }
      //   .zdf {
      //     span {
      //       color: #44c08f;
      //     }
      //   }
      // }
      // &.down {
      //   .total {
      //     color: #F43364;
      //   }
      //   .zdf {
      //     span {
      //       color: #dc006c;
      //     }
      //   }
      // }
      .headerInfoLeft {
        flex: 0 0 2.2rem;
        height: 0.95rem;
        .total {
          font-size: 0.56rem;
          font-weight: 700;
        }
        .zdf {
          margin-top: 0.04rem;
          font-size: 0.24rem;
          color: #6a7c9a;
        }
        .red {
          color: #dc006c !important;
        }
        .green {
          color: #44c08f !important;
        }
      }
      .headerInfoRight {
        //  background: red;
        text-align: right;
        height: 100%;
        flex: 0 0 3rem;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        font-size: 0.26rem;
        .labWap {
          width: 100%;
          display: flex;
          padding: 0.2rem 0 0 0;
          flex-direction: column;
          div {
            flex: 1;
            display: flex;
            height: 0.5rem;
            line-height: 0.5rem;
            .text {
              display: inline-block;
              flex: 0 0 1rem;
              // text-align: right;
              //   margin-right: 0.2rem;
              color: #6a7c9a;
              //  margin-right: 0.2rem;
            }
            .num {
              display: inline-block;
              flex: 1;
              //  text-align: center;
              color: #fff;
            }
          }
        }
        .line {
          display: flex;
          width: 3rem;
          padding-bottom: 0.1rem;
          &:last-child {
            padding-bottom: 0;
          }
          .label {
            color: #79799d;
            padding-right: 0.15rem;
